body {
  font-family: 'Titillium Web', sans-serif;
  text-align: center;
}
.container {
  position: relative;
  margin-top: 25px;
  lost-center: 980px;
}
.row:nth-of-type(6) .col {
  background: #b0ce3d;
}
.row:nth-of-type(6) .col .col {
  background: #a1be30;
}
.col {
  position: relative;
  box-sizing: border-box;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background: #bad455;
}
.background-grid {
  position: absolute;
  width: 100%;
  top: -15px;
  left: 0;
}
.background-grid .bg-col {
  height: 480px;
  background: #f1f6dd;
}
